      } else if (val.length === 0) {
        this.readyTitleMessages = '0/10'
      } else {
        this.readyTitleMessages = ' ' + val.length + '/10'
      }
    },
    readySubtitle (val) {
      if (val.length > 10) {
        this.readySubtitleMessages = ' ' + val.length + '/10'
      } else if (val.length === 0) {
        this.readySubtitleMessages = '0/10'
      } else {
