  methods: {
    GetReadyOutlineReadOnly () {
      let text = ''
      this.readyOutline.forEach((chapter, ch) => {
        text = text + 'CH.' + (ch + 1) + '<span class="px-3"> </span>' + $('#chapter' + ch).val() + '<br>'
        chapter.content.forEach((subsection, sub) => {
          text = text + '<span class="px-3"> </span>' + (ch + 1) + '-' + (sub + 1) + '<span class="px-3"> </span>' + $('#subsection' + ch + '_' + sub).val() + '<br>'
        })
      })
      this.readyOutlineReadOnly = text
    },
    selectReadyCardBg () {
      if (this.readyCardBgUpload[0].url) {
        this.readyCardBg = '3'
      }
    },
    uploadReadyCardBg () {
      if (!this.readyCardBgUpload[0].url) {
        $('#file_readyCardBg').click()
      }
    },
    setReadyCardBg (e) {
      let files = e.target.files || e.dataTransfer.files
      if (e.srcElement.files.item(0).size > (4 * 1024 * 1024)) {
        $('#file_readyCardBg').val('')
        alert('4MB')
      } else {
        // alert('Uploading: ' + e.srcElement.files.item(0).name + ' @ ' + e.srcElement.files.item(0).size + 'bytes')
        // console.log(window.URL.createObjectURL(e.srcElement.files.item(0)))
        // console.log(e.srcElement.files.item(0).name)
        // let target = e.target
        // console.log(this.uploadIMG[i].url)
        // this.uploadIMG[i].url = this.getFileUrl(e.srcElement)
        this.readyCardBgUpload[0].url = window.URL.createObjectURL(e.srcElement.files.item(0))
        this.readyCardBgUpload[0].file = files[0]
        this.readyCardBgUpload[0].name = e.srcElement.files.item(0).name
        this.readyCardBgList[3] = this.readyCardBgUpload[0].url
        // $('#img_' + target.id).attr('src', this.getFileUrl(e.srcElement))
      }
    },
    cancelReadyCardBg () {
      this.readyCardBgUpload[0].file = this.readyCardBgUpload[0].name = this.readyCardBgUpload[0].url = ''
      this.readyCardBgList[3] = ''
      $('#file_readyCardBg').val('')
      // this.createImage(files[0])
    },
    dosubmitVerifyBefore () {
      this.submitVerify = true
    },
    doSubmitVerify () {
      if (!this.readyTitleVerify &&
      !this.readySubTitleVerify) {
        this.submit = true
      } else {
        this.submit = false
        scroll(0, 0)
        this.snackbar = true
        this.color = 'red'
        this.errorMessages = ''
      }
    },
    dosubmitVerifyAfter () {
      if (this.submit) {
        let formData = new FormData()
        // formData.append('readyEmail', this.readyEmail)
        // formData.append('readyPwd', this.readyPwd)
        // formData.append('readyMobile', this.readyMobile)
        // formData.append('readyName', this.readyName)
        // formData.append('readyNickname', this.readyNickname || this.readyName)
        // formData.append('readyGender', this.readyGender)
        // formData.append('tutor_experience', this.readyYear)
        // formData.append('readyBirth', this.readyBirth)
        // formData.append('intro', this.readyIntro)
        // formData.append('subject_type', this.readyTag)
        // formData.append('education_1', this.readyEdu1)
        // formData.append('education_2', this.readyEdu2)
        // formData.append('education_3', this.readyEdu3)
        // formData.append('company', this.readyJob)
        // formData.append('special_exp', this.readyExp)
        // formData.append('teacher_general_availabale_time', this.readySelectTime)
        // formData.append('upload_snapshot', this.readyHeadshot[0].file)
        // this.uploadIMG.forEach((up) => {
        //   formData.append('upload_cer', up.file)
        // })
        this.$http.post('api/', formData).then(res => res.data)
          .then(res => {
            if (res.status === 'success') {
              this.snackbar = true
              this.color = 'green'
              this.errorMessages = '1~3'
              this.$router.push('/')
            } else if (res.status === 'failure') {
              this.snackbar = true
              this.color = 'red'
              this.errorMessages = res.errMsg
            } else {
              this.snackbar = true
              this.color = 'orange'
              this.errorMessages = ''
            }
          })
          .catch(err => {
            this.snackbar = true
            this.color = 'orange'
            this.errorMessages = ''
            console.log(err)
          })
      }
    },
    async doSubmit () {
      await this.dosubmitVerifyBefore()
      await this.doSubmitVerify()
      await this.dosubmitVerifyAfter()
    }
  }
}
