<template>
  <v-container
    id="timeselector"
    p-0
    m-0>
    <v-layout justify-center>
      <v-flex
        xs12>
        <div class="are-alert m-0 light-blue">
          <span class="are-alert-title"></span>
        </div>
        <div
          v-if="selectWeekView"
          class="selector-time-week-title">
          <div
            :class="{ 'active': selectWeek === 0 }"
            class="cursor"
            @click="selectWeek=0"></div>
          <div
            :class="{ 'active': selectWeek === 1 }"
            class="cursor"
            @click="selectWeek=1"></div>
          <div
            :class="{ 'active': selectWeek === 2 }"
            class="cursor"
            @click="selectWeek=2"></div>
          <div
            :class="{ 'active': selectWeek === 3 }"
            class="cursor"
            @click="selectWeek=3"></div>
          <div
            :class="{ 'active': selectWeek === 4 }"
            class="cursor"
            @click="selectWeek=4"></div>
          <div
            :class="{ 'active': selectWeek === 5 }"
            class="cursor"
            @click="selectWeek=5"></div>
          <div
            :class="{ 'active': selectWeek === 6 }"
            class="cursor"
            @click="selectWeek=6"></div>
        </div>
        <div class="selector-time-item-area">
          <div
            v-for="(time, t) in selectTimeList[selectWeek]"
            :key="'time' + t"
            :class="{ 'active': time.select }"
            class="cursor"
            @click="doSelectTime(selectWeek, time.value, time.select)">{{ time.text }}</div>
        </div>
        <!-- <pre style="word-wrap: break-word; white-space: pre-wrap;">{{ msg }}</pre> -->
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  name: 'TimeSelector',
  props: {
    // resConfirm: {
    //   type: Boolean,
    //   default: true
    // },
    selectWeekView: { // 
      type: Boolean,
      default: true
    },
    selectTimeDefault: { // 
      type: Array,
      default: null
    }
  },
  data: function () {
    return {
      selectWeek: 0,
      selectTimeList: []
    }
  },
  computed: {
    selectTime () {
      let res = ''
      let week = [6, 0, 1, 2, 3, 4, 5]
      this.selectTimeList.forEach((w, index) => {
        let array = []
        w.forEach((t) => {
          if (t.select) {
            array.push(t.value)
          }
        })
        if (array.length > 0) {
          res = res + (week[index] + ':' + array.join() + ';')
        }
      })
      return res
    }
  },
  // watch: {
  //   resConfirm (val) {
  //     if (val) {
  //       this.$emit('resSelectTime', this.selectTime)
  //     }
  //   }
  // },
  // Sends action to Vuex that will log you in and redirect to the dash otherwise, error
  mounted () {
    for (let w = 0; w < 7; w++) {
      this.selectTimeList.push([])
      for (let t = 0; t < 48; t++) {
        let select = false
        //  selectTimeDefault
        this.selectTimeList[w].push({ select: select, value: t, text: (((t < 20) ? '0' : '') + parseInt(t / 2) + ':' + ((t % 2) ? '30' : '00') + '-' + (((t + 1) < 20) ? '0' : '') + parseInt((t + 1) / 2) + ':' + (((t + 1) % 2) ? '30' : '00')) })
      }
    }
  },
  methods: {
    doSelectTime (week, code, status) {
      this.selectTimeList[week].forEach((t) => {
        if (t.value === code) {
          t.select = !status
        }
      })
      this.$emit('resSelectTime', this.selectTime)
    }
  }
}
</script>
